@use 'base/var' as v;
@use 'base/mixins' as m;

.boletos{
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$blanco;
        margin-bottom: 2rem;
    }

    .contenedorBoletos{
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    }
}

.pase{
    .nombrePase{
        text-align: center;
        color: v.$blanco;
        font-size: 2.8rem;
    }
    &Header,
    &Footer{
        p{
            text-align: center;
            color: v.$blanco;
            margin: 0;
        }
    }
    &Header{
      background-color: v.$rosa;
      p{
        text-transform: uppercase;
        font-size: 1.6rem;
      }
    }
    &Body{
        background-color: v.$blanco;
        ul{
            @include m.resetearLista;
            li,
            :nth-child(odd){
                background-color: v.$blanco;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &Footer{
        background-color: v.$morado;
        p{
            font-size: 2.4rem;
            font-weight: bold;
        }
    }
}