@use 'base/var' as v;
@use 'base/mixins' as m;

.video{
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay{
        position: absolute;
        background: rgb(123,123,246);
        background: linear-gradient(90deg, rgba(123,123,246,0.5) 15%, rgba(255,153,225,0.5046393557422969) 65%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        z-index: 10;
        .contenidoVideo{
            h2{
                color: v.$blanco;
                font-size: 7rem;
                margin: 0;
                font-weight: bold;
            }
            p{
                color: v.$blanco;
                background-color: v.$rosa;
                display: inline-block;
                padding: 1rem 2rem;
                font-weight: bold;
                font-size: 2.4rem;
                margin: 0;
            }
        }
    }
    video{
        width: auto;
        @include m.desktop {
            width: 100%;
        }
        display: block;
    }
}

