@use 'base/var' as v;
@use 'base/mixins' as m;

.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;
    h3,p{
        text-align: center;
        color: v.$blanco;
    }
    .dia{
        text-transform: uppercase;
        margin: 5rem 0;
    }
}

.escenariosContenedor{
    @include m.tablet{
        @include m.grid(2, 5rem);
    }
    p{
        color: v.$negro;
    }
    .escenario{
        padding-top: 2rem;
        .nombreEscenario{
            &:after{
                content: '';
                display: block;
                width: 20rem;
                height: 10rem;
                background-size: 15rem;
                background-repeat: no-repeat;
                background-position: center center;
                margin: 2rem auto 0 auto;

            }
        }
    }
}

.calendario{
    @include m.resetearLista;
    li{
        padding: 2.5rem;
        font-weight: bold;
        span{
            font-weight: normal;
        }
    }
}

.edm .nombreEscenario:after{
    background-image: url(../img/icono_edm.png);
}
.rock .nombreEscenario:after{
    background-image: url(../img/icono_rock.png);
}

.bgVerde{
    background-color: v.$verde;
    li:nth-child(odd){
        background-color: darken(v.$verde, 10%);
    }
}
.bgAmarillo{
    background-color: v.$amarillo;
    li:nth-child(odd){
        background-color: darken(v.$amarillo, 25%);
    }
}