@use 'base/var' as v;
@use 'base/mixins' as m;

.sobreFestival{
    padding: 5rem 0;
    @include m.tablet{
        @include m.grid(2, 5rem);
    }
    .contenidoFestival {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 5rem;
        @include m.tablet {
            margin-top:0;
        }
        h2 {
            margin-bottom: 2rem;
    
        }
        .fecha {
            color: v.$rosa;
            font-weight: bold;
        }
    }
}