@use 'base/var' as v;
@use 'base/mixins' as m;

header{
    background-color: v.$verde;
    @include m.tablet{
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }
    .contenidoHeader{
        padding: 2rem;
        margin: 0;
        align-items: center;
        @include m.tablet{
            display: flex;
            justify-content: space-evenly;
        }
        h1{
            text-align: center;
            @include m.tablet{
                margin: 0;
            }
        }
        .navegacionPrincipal{
            @include m.tablet{
                display: flex;
                gap: 2rem;
            }
            a{
                @include m.tablet{
                    margin: 0;
                }
                display: block;
                text-align: center;
                margin-bottom: 1rem;
                color: v.$blanco;
                font-size: 2.4rem;
                &:hover{
                    color: v.$amarillo;
                }
            }
        }
    }
}