@use 'base/var' as v;
@use 'base/mixins' as m;

.galeria{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}

.galeriaImagenes{
    @include m.resetearLista;
    @include m.grid(2, 2rem);
    @include m.telefono{
        @include m.grid(3, 2rem);
    }
    @include m.tablet{
        @include m.grid(4, 2rem);
    }
    @include m.desktop{
        @include m.grid(6, 2rem);
    }
}

.overlay{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    background-color: rgb(0 0 0 / .5);
    picture{
        display: block;
        max-width: 80rem;
    }
}

.btnCerrar{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    color: v.$negro;
    background-color:  v.$rosa;
    display: grid;
    place-items: center;
    font-weight: bold;
    &:hover{
        cursor: pointer;
    }
}